// install (please make sure versions match peerDependencies)
// yarn add @nivo/core @nivo/pie
import * as React from 'react';
import { ResponsivePie } from '@nivo/pie';

const Pie = () => {
  const handle = {
    padClick: (data) => {
      console.log(data);
    },
    legendClick: (data) => {
      console.log(data);
    },
  };

  return (
    // chart height 100%  chart    height 
    <div className='pie-div' >
      <ResponsivePie
        /* chart   */
        data={[
          { id: 'Safe Center', value: 68 },
          { id: `Volunteer Work`, value: 21 },
          { id: 'Management', value: 11 },
        ]}

        /* chart margin */
        margin={{ top: 25, right: 150, bottom: 60, left: 150 }}

        /* chart    */
        innerRadius={0.5}

        /* pad  */
        padAngle={1.8}
        
        /* pad radius  (pad    ) */
        cornerRadius={5}
              
        /* chart  */
        colors={['#C9BBCF', '#B7D3DF', '#D6EFED']} //   
              
        // colors={{ scheme: 'nivo' }} // nivo     
              
        /* pad border   */
        borderWidth={2}

        /* link label skip   */
        arcLinkLabelsSkipAngle={0}

        /* link label  */
        arcLinkLabelsTextColor="#EFEFEF"
              
        /* link label    */
        arcLinkLabelsThickness={1.8}

        /* link label    */
        arcLinkLabelsColor={{ from: 'color' }} // pad  

        /* label (pad  ) skip   */
        arcLabelsSkipAngle={10}

        theme={{
                /* label style (pad  ) */
                labels: {
                  text: {
                          fontSize: 14,
                          fill: '#112B3C',
                        },
                      },
                /* legend style (default    key ) */
                legends: {
                  text: {
                          fontSize: 11,
                          fill: '#ffffff',
                        },
                      },
              }}

        /* pad   */
        onClick={handle.padClick}

        /* legend  (default    key ) */
        legends={[
            {
              anchor: 'bottom', // 
              direction: 'column', // item  
              justify: false, // ,   justify  
              translateX: 120, // chart X 
              translateY: 70, // chart Y 
              itemsSpacing: 5, // item 
              itemWidth: 70, // item width
              itemHeight: 18, // item height
              itemDirection: 'left-to-right', // item   
              itemOpacity: 0, // item opacity
              symbolSize: 18, // symbol ( ) 
              symbolShape: 'circle', // symbol ( ) 
              effects: [
                {
                  //    (hover textColor olive )
                  on: 'hover',
                  style: {
                    itemTextColor: 'gray',
                  },
                },
              ],
              onClick: handle.legendClick, // legend  
          },
        ]}
      />
      </div>
  );
};

export default Pie;